.cart{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cartItems{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cartItem{
    width: 700px;
    height: 250px;
    display: flex;
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px;
}

.cartItem img{
    width: 200px;
    height: 200px;
}

.cartItem .description{
    width: 100%;
    font-size: 24px;
}

.countHandler input{
    width: 40px;
    text-align: center;
    font-weight: bolder;
}

.checkout button{
    width: 150px;
    height: 50px;
    background-color: #222;
    color: #fff;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
}

@media screen and (max-width: 750px) {
    .cartItem{
        width: 80%;
        margin: auto;
    }
    .cartItem img{
        width: 40%;
    }
}