.shopTitle{
    margin-top: 100px;
    text-align: center;
    font-size: 30px;
}

.products{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    /* grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); */
    grid-gap: 20px;
    place-items: center;
}

.product{
    border-radius: 15px;
    width: fit-content;
    /* height: 350px; */
    margin: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    padding: 20px 0;
}

.product img{
    width: 350px;
    height: 275px;
}

.product .description{
    text-align: center;
}

.product:hover{
    transition: 0.3s ease-in;
    cursor: pointer;
}

.addToCartBtn{
    background-color: transparent;
    /* border: #222; */
    min-width: 100px;
    padding: 5px 10px;
    border-radius: 15px;
}

.addToCartBtn:hover{
    background-color: #222;
    color: #fff;
    cursor: pointer;
}

@media screen and (max-width: 900px){
    .products{
        display: flex;
        flex-direction: column;
    }
}

@media screen and (max-width: 400px){
    .product img{
        width: 250px;
        object-fit: contain;
    }
}